@charset "utf-8";

.menu_list ul{
  list-style: none;
  padding: 0px;
  display: flex;
}

.menu_list li{
  display:inline-block;
  margin: 5px 20px;
  padding:5px 20px;
  cursor: pointer;
  border: 1px solid #ccc;
  background: #f0f0f0;
  margin-right: 5px;
}

.menu_list li:hover{
  background: #ddd;
}
.menu_list li.act{
  background: #007bff;
  color: #fff;
  font-weight: bold;
}

.tab_content{
  padding:20px;
}
.content{
  width: 500px; height: 200px;
  background: #eee;
}