.product_list{
  width:1200px;
  margin:0 auto;
  display:flex;
  justify-content: space-between;
  padding: 0px;
  list-style: none;
}

.product_list li{
  width: 24%;
  margin: 0px;
}
.product_list li > img{
  width: 100%;
}

.product_title{font-weight: bold;}
.product_price{
  font-weight: bold;
  color: #f00;
  display: block;
  text-align: right;
}
