@charset "utf-8";

.k_logo{
  text-align: center;
  margin: 50px 0px;
}
.k_hr{
  width: 1050px;
  margin: 0 auto;
}

.md_title{
  text-align: center;
  color: #333;
  margin: 100px 0px 50px 0px;
  font-size: 1.875rem;
}
.k_mnu{
  width: 100%;
  max-width: 1050px;
  margin:0 auto;

  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
}
.k_mnu > li{
  background: #F7F7F7;
  color: #333;
  font-size: 0.875rem;
  padding: 10px 20px;
  border-radius: 22px;
  margin: 0px 5px 20px;
  cursor: pointer;
}
.k_mnu > li.active{
  background: rgb(95, 0, 128);
  color: #fff;
}

.Product_wrap{
  display: flex;
  margin: 50px 0px;
  gap: 20px;
  justify-content: center;
}
.Product{
  width: 249px;
}
.Product > div {
  width:100%;
  max-width: 249px;
  max-height: 324px;
  overflow: hidden;
  position: relative;
}

.Product > div > img{
  width: 100%;
  transition: 0.3s;
}
.Product > div > img:hover{
  transform: scale(1.05);
}

.Product > .k_name{
  margin-bottom: 10px;
}
.Product > .k_price{
  color: #ccc;
  text-decoration: line-through;
  font-size: 0.875rem;
}
.Product > p > .k_percent{
  color: rgb(250, 98, 47);
  margin-right: 8px;
}
.Product > .k_total{
  font-weight: bold;
}

.slide > img{
  width: 100%;
}

.k_cart{
  position: absolute;
  width: 35px; line-height: 35px;
  text-align: center;
  border-radius: 50%;
  background: rgba(95, 0, 128, 0.5);
  color: rgba(255, 255, 255, 0.5);
  bottom: 10px;
  right: 10px;
}

.rbanner_wrap{
  max-width: 1050px;
  margin:0 auto;
}
.rbanner_wrap > img{
  width: 100%;
}